












































































import { Component, Vue, Prop, Emit, Watch, Ref } from 'vue-property-decorator';
import { viewMailTemp } from '@api/flowManage';
import { namespace } from 'vuex-class';
const eventRegiste = namespace('eventRegiste');
const processTemplate = namespace('processTemplate');
@Component({
  components: {}
})
export default class RghMenu extends Vue {
  // @Prop({ default: false }) dialogVisible!: boolean;
  // @Prop() optionsCge!: (val: Array<any>) => void;
  @Prop({ default: '' }) substanceText!: string;
  @Prop({ default: '' }) theme!: string;
  @Prop({ default: '90%' }) width!: string;
  @Prop({ default: '240px' }) height!: string;
  @Prop({ default: false }) manual!: boolean;
  @Prop({ default: [] }) manualRghTree!: Array<any>;
  @Watch('substanceText', { deep: true, immediate: true })
  ChangeSubstanceText(newVal: string, oldVal: string) {
    this.setSubstanceText(newVal);
  }

  refreshContent() {
    if (this.substanceText) {
      this.setSubstanceText(this.substanceText);
    }
  }

  @Watch('addValue')
  onChangeValue(newVal: string, oldVal: string) {
    if (newVal == '') {
      return;
    }
    this.listClick();
    this.testrCgeFun();
  }
  @Watch('cgeValue')
  onChangeValue2(newVal: string, oldVal: string) {
    this.changeTag();
    this.testrCgeFun();
  }

  @processTemplate.Getter('flowVariables') flowVariables: any;

  ids: any = null;
  visiblePop = false;
  visiblePopcge = false;
  i = 1;
  sel: any = '';
  range: any = '';
  el: any = '';
  frag: any = '';
  node: any = '';
  lastNode: any = '';
  dataViewsTree: Array<any> = [];
  dataViewsTreeCge: Array<any> = [];
  addValue = '1';
  cgeValue = '1';
  tag: any = '';
  addId = 100;
  addNode: any = [];
  cgeNode: any = [];
  accessoryNode: any = [];
  treeProps: any = {
    children: 'children',
    label: 'text'
  };
  ruleArray: Array<any> = [];

  mounted() {
    // this.init();
  }
  setSubstanceText(newVal: string) {
    this.$nextTick(function() {
      const substance: any = document.querySelectorAll('#test')[0];
      substance.innerText = newVal;
    });
  }

  cgeConfirm() {
    if (this.cgeNode.value) {
      this.cgeValue = '${' + this.cgeNode.value + '}';
    }
  }
  addConfirm() {
    if (this.addNode.value) {
      this.addValue = '${' + this.addNode.value + '}';
    }
  }
  closevisiblePop() {
    const rghul: any = document.querySelectorAll('.rgh_ul')[0];
    rghul.style.display = 'none';
    this.visiblePop = false;
  }

  closevisible() {
    const rghulTag: any = document.querySelectorAll('.rgh_ul_span')[0];
    rghulTag.style.display = 'none';
    this.visiblePopcge = false;
  }
  queryDataViewsFields() {
    if (this.manual) {
      this.dataViewsTree = this.manualRghTree;
      this.dataViewsTreeCge = this.manualRghTree;
    } else {
      this.dataViewsTree = this.flowVariables;
      this.dataViewsTreeCge = this.flowVariables;
    }
  }
  // 清空输入框里的值
  cleanContent() {
    const substance: any = document.querySelectorAll('#test')[0];
    substance.innerText = '';
  }
  testrCgeFun() {
    const substance: any = document.querySelectorAll('#test')[0];
    this.$emit('getContent', substance.innerText);
  }
  init() {
    this.queryDataViewsFields(); // 获取右键树的值
    this.cleanContent(); // 清空输入框的值
    const that = this;
    const testr: any = document.querySelectorAll('#test')[0];
    testr.oninput = function() {
      that.testrCgeFun();
    };
    testr.oncontextmenu = function(ev: any) {
      const rghulTag: any = document.querySelectorAll('.rgh_ul_span')[0];
      rghulTag.style.display = 'none';
      const clintX = ev.clientX; // ev获取的只是屏幕可视范围的x,y值
      const clintY = ev.clientY;
      const scollTop =
        document.documentElement.scrollTop || document.body.scrollTop; // 当有下拉条的时候必须加上当前屏幕不可视范围的left,和top值
      const scollLeft =
        document.documentElement.scrollLeft || document.body.scrollLeft;

      if (window.getSelection) {
        that.sel = window.getSelection();
        if (that.sel.getRangeAt && that.sel.rangeCount) {
          that.range = that.sel.getRangeAt(0);
          that.range.deleteContents();
          that.el = document.createElement('div');
        }
      }
      const rghul: any = document.querySelectorAll('.rgh_ul')[0];
      if (that.sel.anchorNode.parentElement.className == 'slot_html') {
        rghul.style.display = 'none';
        return false;
      }
      rghul.style.left = clintX + scollLeft + 'px';
      rghul.style.top = clintY + scollTop + 'px';
      rghul.style.display = 'block';
      return false;
    };
    const mailDialog: any = document.body;
    mailDialog.onclick = function(e) {
      if (
        e.target.innerText == '插入流程变量' ||
        e.target.innerText == '替换流程变量'
      ) {
        return;
      }
      const rghul: any = document.querySelectorAll('.rgh_ul')[0];
      if (rghul == undefined) {
        return;
      }
      rghul.style.display = 'none';
      const rghulTag: any = document.querySelectorAll('.rgh_ul_span')[0];
      rghulTag.style.display = 'none';
    };
  }
  // 设置弹框树选择
  expandFun(visible) {
    setTimeout(() => {
      this[visible] = false;
      this.$nextTick(() => {
        this[visible] = true;
      });
    }, 400);
  }
  handleNodeClick(node: any) {
    this.addNode = node;
  }
  handleNodeClickcge(node: any) {
    this.cgeNode = node;
  }

  listClick() {
    const that = this;
    that.i++;
    that.el.innerHTML = `<span id="slot${that.i}" contentEditable="false" class="slot_html">${that.addValue}</span>`;
    that.frag = document.createDocumentFragment();
    while ((that.node = that.el.firstChild)) {
      that.lastNode = that.frag.appendChild(that.node);
    }
    that.range.insertNode(that.frag);
    if (that.lastNode) {
      that.range = that.range.cloneRange();
      that.range.setStartAfter(that.lastNode);
      that.range.collapse(true);
      that.sel.removeAllRanges();
      that.sel.addRange(that.range);
    }
    const rghul: any = document.querySelectorAll('.rgh_ul')[0];
    rghul.style.display = 'none';
    this.visiblePop = false;
    this.addValue = '';
    this.addNode = []; // 阻止上次选择后下次不选择直接点击确定扔可添加成功
    // 新增的span标签的右键替换和删除
    const slottag: any = document.querySelectorAll('.slot_html');
    slottag.forEach((item: any) => {
      item.oncontextmenu = function(ev: any) {
        that.tag = item;
        that.slottagFun(item, ev);
        ev.stopPropagation();
        return false;
      };
    });
  }
  changeTag() {
    this.tag.innerText = this.cgeValue;
    const rghulTag: any = document.querySelectorAll('.rgh_ul_span')[0];
    rghulTag.style.display = 'none';
    this.visiblePopcge = false;
  }
  delFun() {
    const del: any = document.getElementById(this.tag.id);
    del.parentNode.removeChild(del);
    this.addValue = '';
  }
  slottagFun(item: any, ev: any) {
    const rghul: any = document.querySelectorAll('.rgh_ul')[0];
    rghul.style.display = 'none';
    const clintX = ev.clientX; // ev获取的只是屏幕可视范围的x,y值
    const clintY = ev.clientY;
    const scollTop =
      document.documentElement.scrollTop || document.body.scrollTop; // 当有下拉条的时候必须加上当前屏幕不可视范围的left,和top值
    const scollLeft =
      document.documentElement.scrollLeft || document.body.scrollLeft;
    const rghulTag: any = document.querySelectorAll('.rgh_ul_span')[0];
    rghulTag.style.left = clintX + scollLeft + 'px';
    rghulTag.style.top = clintY + scollTop + 'px';
    rghulTag.style.display = 'block';
  }
}
