



























































































































































































































































































import { Component, Prop, Vue, Watch, Emit, Ref } from 'vue-property-decorator';
import { dataViewFields } from '@api/viewData';
import { getCommonRuleFuncs } from '@api/processConfig';
import { arrayFind, arrayFindDeep } from '@utils/util';
import { namespace } from 'vuex-class';
import cloneDeep from 'lodash/cloneDeep';
import rghMenu from '@components/RightMenu/RghMenu.vue';
import functionModule from './functionModule.vue';
const processTemplate = namespace('processTemplate');
interface Condition {
  left: string;
  operator: string;
  right: string;
}

interface Regulation {
  logicalOperator: string;
  script: string;
  condition: Condition;
  children: Array<Regulation>;
}

interface FormatDate {
  dateSel: string;
  baseDate: string;
  offsetList: Array<any>;
  showDatePicker: boolean;
}

@Component({ components: { rghMenu, functionModule } })
export default class DecisionRuleEditor extends Vue {
  // 数据视图数据  用来防止刚进入时 数据视图先显示值 等接口获取数据后在现实文字
  @Prop({ default: [] }) viewList: Array<any>;

  @Prop({ default: '' }) dataView: string;
  @Prop({ default: [] }) rule: Array<Regulation>;
  @Prop({ default: false }) all: boolean;
  @Prop({ default: false }) ifEvent: boolean;
  @Prop({ default: '' }) modeType: string;
  @Prop({ default: false }) isMonitor: boolean;
  @Prop({ default: false }) manual: boolean;
  @Prop({ default: '触发条件设置' }) eventName: string;
  @Prop({ default: false }) detailEdit: boolean;
  @processTemplate.Getter('flowVariables') flowVariables: any;
  @Ref() rghMenu: any;
  @Emit('resetItem')
  resetItem() {}
  private dataViewTree: Array<any> = [];
  private dataViewList: Array<any> = [];
  private formatFlowVariables: Array<any> = [];
  private editorRuleTree: Array<Regulation> = [];
  private options: Array<any> = [
    { value: true, label: '是' },
    { value: false, label: '否' }
  ];
  private dataViewId = '';
  private oldDataViewId = '';
  private substanceText = '';
  private dragNodeData: any; //拖动的节点
  private defaultProps: object = { label: 'fieldName' };
  private actionType: Array<any> = [
    { name: '并且(逻辑与)', value: '&&' },
    { name: '或者(逻辑或)', value: '||' }
  ];
  private connectortype: Array<any> = [
    { name: '=', value: '==' },
    { name: '>', value: '>' },
    { name: '<', value: '<' },
    { name: '!=', value: '!=' },
    { name: '>=', value: '>=' },
    { name: '<=', value: '<=' }
  ];
  private dateUnitList: Array<any> = [
    { value: 1, label: '年' },
    { value: 2, label: '月' },
    { value: 3, label: '日' }
  ];
  private dateUnitListQuarter: Array<any> = [
    { value: 4, label: '工作日' },
    { value: 5, label: '自然日' }
  ];

  private dateUnitListMonth: Array<any> = [
    { value: 6, label: '工作日' },
    { value: 7, label: '自然日' }
  ];
  private formatDate: FormatDate = {
    dateSel: '',
    baseDate: '',
    offsetList: [],
    showDatePicker: true
  };

  private overuseDecisions: Array<any> = [
    {
      fieldName: '常用字段',
      root: true,
      children: [
        {
          fieldCode: '当前日期()',
          fieldName: '当前日期',
          fieldType: 2,
          format: 0
        },
        {
          fieldCode: '当前时间()',
          fieldName: '当前时间',
          fieldType: 0,
          format: 0
        },
        {
          fieldCode: '当前日期时间()',
          fieldName: '当前日期时间',
          fieldType: 0,
          format: 0
        },
        {
          fieldCode: '当前月份()',
          fieldName: '当前月份',
          fieldType: 1,
          format: 0
        },
        {
          fieldCode: '当前年份()',
          fieldName: '当前年份',
          fieldType: 1,
          format: 0
        },
        {
          fieldCode: '当前天数()',
          fieldName: '当前天数',
          fieldType: 1,
          format: 0
        },
        {
          fieldCode: 'true',
          fieldName: '是',
          fieldType: 0,
          format: 0
        },
        {
          fieldCode: 'false',
          fieldName: '否',
          fieldType: 0,
          format: 0
        }
      ]
    }
  ];
  private commonEvent: any = {
    fieldName: '常用函数',
    root: true,
    children: []
  };

  private monitorOptions: Array<any> = [
    {
      fieldName: '监控结果',
      root: true,
      children: [
        {
          fieldCode: 'totalCount',
          fieldName: '总条数',
          fieldType: 0
        },
        {
          fieldCode: 'isAllMatched',
          fieldName: '是否全部匹配',
          fieldType: 0
        },
        {
          fieldCode: 'matchedCount',
          fieldName: '匹配条数',
          fieldType: 0
        },
        {
          fieldCode: 'notMatchedCount',
          fieldName: '不匹配条数',
          fieldType: 0
        }
      ]
    },
    {
      fieldName: '常用决策',
      root: true,
      children: [
        {
          fieldCode: 'true',
          fieldName: '是',
          fieldType: 0
        },
        {
          fieldCode: 'false',
          fieldName: '否',
          fieldType: 0
        }
      ]
    }
  ];

  private manualRghTree: Array<any> = []; // 发起方式的右键菜单

  @Watch('dataView', { deep: true, immediate: true })
  getParams(newVal) {
    this.reset();
    if (JSON.stringify(newVal) !== '') {
      this.queryCommonRules();
      if (this.dataViewTree.length === 0) {
        if (newVal) {
          this.dataViewId = newVal;
          // 由于直接赋值不触发select的change事件，所以手动调用一次
          this.dataViewQuery(this.dataViewId, true);
        }
      }
    }
  }

  @Watch('manual', { deep: true, immediate: true })
  watchContentIf(newVal) {
    if (newVal) {
      this.$nextTick(() => {
        this.rghMenu.init(); // 组件右键菜单初始化
        this.rghMenu.refreshContent();
      });
    }
  }

  @Watch('dataViewId')
  watchDataViewId(newVal, oldval) {
    this.oldDataViewId = oldval;
  }

  @Watch('rule', { deep: true, immediate: true })
  watchRuleSwitch(newVal) {
    this.substanceText = '';
    if (newVal && newVal.length) {
      this.substanceText = newVal[0].script ? newVal[0].script : '';
      if (this.ifEvent && this.dataViewTree.length === 0) {
        this.formatFlowVariablesFun();
      } else if (!this.ifEvent && this.dataViewTree.length === 0) {
        this.dataViewQuery(this.dataViewId, true);
      } else {
        this.refreshEditorTree(true);
      }
    } else {
      const root = this.createRegulation();
      root.logicalOperator = '&&';
      this.editorRuleTree = [root];
    }
  }

  @Watch('editorRuleTree', { deep: true, immediate: true })
  syncRuleChange() {
    if (this.dataViewId) {
      this.$emit('change', this.dataViewId, this.editorRuleTree);
    }
  }
  // 监控设置时，同一页面公用两种决策编辑器，此时需要重新调一下数据视图列表
  @Watch('all', { immediate: true })
  allChange() {
    this.getViewDataOptions();
  }
  @Watch('detailEdit')
  detailEditChange() {
    if (this.detailEdit) {
      this.detailEdit = true;
    }
  }

  /**
   * 对外方法：返回当前规则设置
   */
  getCurrentRule() {
    if (!this.ifEvent) {
      return {
        dataViewId: this.dataViewId,
        ruleArray: this.handleSaveConcatParams(this.editorRuleTree[0])
      };
    } else {
      return this.handleSaveConcatParams(this.editorRuleTree[0]);
    }
  }

  createRegulation() {
    return {
      script: '',
      logicalOperator: '&&',
      condition: {
        left: '',
        operator: '',
        right: ''
      },
      children: []
    };
  }

  getContent(val: string) {
    this.editorRuleTree[0].script = val;
  }

  // 系统当日->后面的日期选择器不显示，设置为'getDate()'
  dateSelChange(params) {
    if (params.dateSel === 'systemNow') {
      params.showDatePicker = false;
      params.baseDate = 'getDate()';
    } else if (params.dateSel === 'currentQuarter') {
      params.showDatePicker = false;
      params.baseDate = '';
      params.offsetList = [
        {
          type: 4,
          sign: '+',
          offset: 1
        }
      ];
      return;
    } else if (params.dateSel === 'currentMonth') {
      params.showDatePicker = false;
      params.baseDate = '';
      params.offsetList = [
        {
          type: 6,
          sign: '+',
          offset: 1
        }
      ];
      return;
    } else if (params.dateSel === 'selDate') {
      params.showDatePicker = true;
      params.baseDate = '';
    }
    params.offsetList = [
      {
        type: 3,
        sign: '+',
        offset: 1
      }
    ];
  }

  leftIpt(data) {
    this.$set(data.condition, 'fieldType', 'text');
    data.condition.left = `"${data.condition.leftIpt}"`;
  }

  rightIpt(data) {
    data.condition.right = `"${data.condition.rightIpt}"`;
  }

  dateUnitChange(val, index, offsetList) {
    // 判断前面是否选过此单位，避免出现 +5月, +4月的情况
    let canPush = true;
    if (index !== 0) {
      let num = 0;
      offsetList.forEach((item) => {
        if (item.type > val) {
          this.$message.info(`请保证"年"偏移量在"月"前面设置`);
          offsetList.splice(index, 1);
          canPush = false;
          return false;
        }
        if (item.type === val) num++;
      });
      if (num > 1) {
        const msg: any = this.$message;
        // type: 1-年， 2-月，3-日
        const unit =
          val === 1 ? '年' : val === 2 ? '月' : val === 3 ? '日' : '';
        msg.closeAll();
        this.$message.info(`已经选择过"${unit}"的偏移量`);
        offsetList.splice(index, 1);
        canPush = false;
        return false;
      }
    }
    if (canPush && val !== 3) {
      offsetList.push({
        type: 3,
        sign: '+',
        offset: 1
      });
    }
  }
  formatTreeData(ruleList, root) {
    const treeArr: Array<Regulation> = [];
    if (ruleList && ruleList.length > 0) {
      ruleList.forEach((item) => {
        const obj: Regulation = this.createRegulation();
        obj.logicalOperator = item.logicalOperator;
        if (item.children && item.children.length > 0) {
          obj.children = this.formatTreeData(item.children, false);
        } else if (item.condition) {
          const tempCondition = item.condition;
          // targetDateView 左侧树的值，此时有三种情况 1.isMonitor 监控节点的自有的固定数据monitorOptions
          // 2.ifEvent=true, 固定数据formatFlowVariables，3.ifEvent=false，根据数据视图id获取的dataViewTree
          const targetDateView = this.isMonitor
            ? this.monitorOptions
            : this.ifEvent
            ? this.formatFlowVariables
            : this.dataViewTree;
          const targetObj = arrayFindDeep(
            targetDateView,
            'fieldCode',
            tempCondition.left
          );
          if (targetObj) {
            this.$set(tempCondition, 'fieldName', targetObj.fieldName);
            this.$set(tempCondition, 'fieldType', targetObj.fieldType);
          }
          obj.condition = tempCondition;
        }
        treeArr.push(obj);
      });
    } else {
      if (root) {
        treeArr.push(this.createRegulation());
      }
    }

    return treeArr;
  }
  // 获取数据视图筛选列表
  getViewDataOptions() {
    const param: any = {};
    param.type = '0';
    if (!this.all) {
      param.dictFlag = '0';
    }

    dataViewFields(param).then(({ data, error }) => {
      if (!error) {
        this.dataViewList = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }

  queryCommonRules() {
    getCommonRuleFuncs().then(({ data, error }) => {
      if (!error) {
        const arr: any = [];
        data.forEach((item) => {
          arr.push({
            fieldCode: item.methodName,
            fieldTypeEvent: 4,
            fieldName: item.methodAlias,
            paramTypeList:
              item.paramTypeList instanceof Array &&
              item.paramTypeList.length > 0
                ? item.paramTypeList.map((param) => ({
                    ...param,
                    paramValue: '',
                    argumentsValue: ''
                  }))
                : []
          });
        });

        this.commonEvent.children = arr;
        if (!arrayFind(this.formatFlowVariables, 'fieldName', '常用函数')) {
          this.formatFlowVariables.push(this.commonEvent);
        }
        if (!arrayFind(this.dataViewTree, 'fieldName', '常用函数')) {
          this.dataViewTree.push(this.commonEvent);
        }
        if (!arrayFind(this.monitorOptions, 'fieldName', '常用函数')) {
          this.monitorOptions.push(this.commonEvent);
        }
      }
    });
  }

  // 获取数据左侧数据视图
  async dataViewQuery(newVal, isSplitEditorTree) {
    await dataViewFields({ type: '0' }).then(({ data, error }) => {
      if (!error) {
        this.doViewDataFieldsGet(data, newVal, isSplitEditorTree);
      }
    });
  }

  doViewDataFieldsGet(datas, id, isSplitEditorTree) {
    this.dataViewTree = [...this.overuseDecisions];
    this.manualRghTree = [];
    if (datas.length) {
      const list = arrayFind(datas, 'id', id);
      if (list) {
        const concatList = this.concatId(id, list);
        this.dataViewTree.unshift({
          fieldName: concatList.name,
          fieldCode: id,
          root: true,
          children: this.splicingViewForeignFields(concatList.fields, id)
        });
        if (this.manual) {
          this.manualRghTree.unshift({
            fieldName: concatList.name,
            fieldCode: id,
            root: true,
            children: this.splicingViewForeignFields(concatList.fields, id)
          });
          this.rghTreeFormat();
        }
      }
      this.refreshEditorTree(isSplitEditorTree);
    }
    this.queryCommonRules();
  }

  splicingViewForeignFields(fields, vid) {
    const uniFields = [...fields];
    const fids: Array<string> = [];
    fields.forEach((fie) => {
      if (fie.foreignId && fids.indexOf(fie.foreignId) === -1) {
        fids.push(fie.foreignId);

        const fv = arrayFind(this.dataViewList, 'id', fie.foreignId);
        if (fv) {
          uniFields.push(
            ...fv.fields.map((ffi) => {
              return {
                ...ffi,
                fieldCode: `${vid}.${ffi.fieldCode}`,
                fieldName: `${fv.name}_${ffi.fieldName}`
              };
            })
          );
        }
      }
    });
    return uniFields;
  }

  rghTreeFormat() {
    function formatArr2(newArr) {
      for (let x = 0; x < newArr.length; x++) {
        newArr[x].text = newArr[x].fieldName;
        newArr[x].value = newArr[x].fieldCode;
        if (newArr[x].children && newArr[x].children.length > 0) {
          newArr[x].children = formatArr2(newArr[x].children);
        }
      }
      return newArr;
    }
    formatArr2(this.manualRghTree);
    this.$nextTick(() => {
      this.rghMenu.queryDataViewsFields(); //流程视图变化右键菜单树同时变化
    });
  }

  // 数据视图中树节点的id，要与父节点进行拼接，格式：1.frequency parentId:父节点id
  concatId(parentId, view) {
    if (view && view.fields && view.fields.length) {
      const node = cloneDeep(view);
      node.fields.forEach((item) => {
        item.fieldCode = `${parentId}.${item.fieldCode}`;
      });
      return node;
    }
    return [];
  }

  addRelation(data) {
    const newChild = this.createRegulation();
    newChild.logicalOperator = '&&';
    if (!data.children) {
      this.$set(data, 'children', []);
    }
    data.children.push(newChild);
  }

  addLeaf(data) {
    const newChild = this.createRegulation();
    newChild.logicalOperator = '';
    newChild.condition['fieldName'] = '';
    newChild.condition['operator'] = '==';
    if (!data.children) {
      this.$set(data, 'children', []);
    }
    data.children.push(newChild);
  }

  remove(node, data) {
    const parent = node.parent;
    let children = [];
    if (data.children && data.children.length) {
      this.$confirm('此操作将永久删除本节点以及所有子节点, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          children = parent.data;
          this.sureDel(children, data);
        })
        .catch(() => {});
    } else {
      children = parent.data.children;
      this.sureDel(children, data);
    }
  }

  refreshEditorTree(splitTree) {
    if (splitTree) {
      const rule = cloneDeep(this.rule);
      this.editorRuleTree = this.formatTreeData(rule, true);
      this.editorRuleTree[0].script = this.substanceText;
    } else {
      const root = this.createRegulation();
      root.logicalOperator = '&&';
      this.editorRuleTree = [root];
      this.substanceText = this.editorRuleTree[0].script;
    }
  }

  sureDel(children, data) {
    const index = children.findIndex((d) => d.$treeNodeId === data.$treeNodeId);
    children.splice(index, 1);
  }

  created() {
    // 如果传入select list 则不走接口 直接赋值
    if (this.viewList.length) {
      this.dataViewList = this.viewList;
    } else {
      this.getViewDataOptions();
    }
    if (!this.formatFlowVariables.length) {
      this.formatFlowVariablesFun();
    }
  }

  formatFlowVariablesFun() {
    this.formatFlowVariables = cloneDeep(this.flowVariables);
    function formatArr2(newArr) {
      for (let x = 0; x < newArr.length; x++) {
        newArr[x].fieldName = newArr[x].label;
        newArr[x].fieldCode = newArr[x].value;
        newArr[x].fieldType = newArr[x].fieldType || 0;
        if (newArr[x].children && newArr[x].children.length > 0) {
          newArr[x].children = formatArr2(newArr[x].children);
        }
      }
      return newArr;
    }
    formatArr2(this.formatFlowVariables);
    if (!arrayFind(this.formatFlowVariables, 'fieldName', '常用时间')) {
      this.formatFlowVariables.push(...this.overuseDecisions);
    }
    this.refreshEditorTree(true);
    this.queryCommonRules();
  }

  reset() {
    this.dataViewId = '';
    this.dataViewTree = [];
    const root = this.createRegulation();
    root.logicalOperator = '&&';
    this.editorRuleTree = [root];
    this.resetItem();
  }

  ruleTreeFun(val) {
    if (val === 'add') {
      this.reset();
    } else {
      // this.editorRuleTree = val;
    }
  }

  queryChange(val) {
    if (this.editorRuleTree[0].children.length) {
      this.$confirm('此操作将清空已编辑的决策编辑器数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.refreshEditorTree(false);
          this.regionCge(val);
        })
        .catch(() => {
          this.dataViewId = this.oldDataViewId;
        });
    } else {
      this.regionCge(val);
      this.queryChangeView(val);
    }
  }

  @Emit('queryViewEmit')
  queryChangeView(val) {
    const server = arrayFind(this.dataViewList, 'id', val);
    return server.fields;
  }

  // 为父组件传值，监控节点-监控设置面板用到
  @Emit('queryParamsEmit')
  regionCge(val: any) {
    let paymentList: Array<any> = [];
    const server = arrayFind(this.dataViewList, 'id', val);
    if (server && server.queryParams) {
      paymentList.push({ fields: server.fields });
      paymentList = server.queryParams.map((param) => {
        return {
          paramCode: param.code,
          paramName: param.name,
          paramType: param.type,
          paramValueName: param.defaultValue,
          paramValue: ''
        };
      });
    }

    if (server) {
      const dv = [...this.overuseDecisions];
      this.manualRghTree = [];
      const concatList = this.concatId(val, server);
      dv.unshift({
        fieldName: concatList.name,
        fieldCode: val,
        root: true,
        children: this.splicingViewForeignFields(concatList.fields, val)
      });
      if (this.manual) {
        this.manualRghTree.unshift({
          fieldName: concatList.name,
          fieldCode: val,
          root: true,
          children: this.splicingViewForeignFields(concatList.fields, val)
        });
        this.rghTreeFormat();
      }
      dv.push(this.commonEvent);
      this.dataViewTree = dv;
    }
    return paymentList;
  }

  // 树形控件在页面内不拖拽
  allowDrop() {
    return false;
  }

  canDrag(node) {
    return node.isLeaf;
  }

  // 页面中同时存在两个数 区别当前操作的是否是同一个树的节点
  dataDragStart(startNode, flagType) {
    if (flagType !== undefined) {
      this.dragNodeData = startNode;
    } else {
      this.dragNodeData = {};
    }
  }

  handleSomethingDrop(node, data) {
    // 当前没有被拖动的对象 返回
    if (Object.keys(this.dragNodeData).length === 0) return false;
    if (this.dragNodeData.data.fieldTypeEvent == 4) return false;
    data.condition.fieldName = this.dragNodeData.data.fieldName;
    data.condition.left = this.dragNodeData.data.fieldCode;
    data.condition.fieldType = this.dragNodeData.data.fieldType;
    this.dragNodeData = {};
  }

  handleSomethingDropEvent(node, data) {
    // 当前没有被拖动的对象 返回
    if (Object.keys(this.dragNodeData).length === 0) return false;
    // if (this.dragNodeData.data.fieldTypeEvent != 4) return false;
    this.$set(
      data.condition,
      'fieldNameEvent',
      this.dragNodeData.data.fieldName
    );
    data.condition.leftEvent = this.dragNodeData.data.fieldCode;
    if (this.dragNodeData.data.fieldTypeEvent == 4) {
      data.condition.fieldTypeEvent = this.dragNodeData.data.fieldTypeEvent;
    } else {
      data.condition.fieldTypeEvent = 'notFun';
    }

    const resetList = cloneDeep(this.dragNodeData.data.paramTypeList);
    if (resetList) {
      resetList.forEach((item) => {
        (item.paramValue = ''),
          (item.argumentsValue = ''),
          (item.argumentsCode = '');
      });
    }
    this.$set(data.condition, 'paramTypeList', resetList);
    this.dragNodeData = {};
  }

  handleSomethingDropArguments(node, data, item) {
    // 当前没有被拖动的对象 返回
    if (Object.keys(this.dragNodeData).length === 0) return false;
    this.$set(item, 'argumentsValue', this.dragNodeData.data.fieldName);
    this.$set(item, 'argumentsCode', this.dragNodeData.data.fieldCode);
    item.fieldTypeEvent = this.dragNodeData.data.fieldTypeEvent;
    item.format = this.dragNodeData.data.format;
    const resetList = cloneDeep(this.dragNodeData.data.paramTypeList);
    if (resetList) {
      resetList.forEach((itemval) => {
        itemval.paramValue = '';
        itemval.argumentsValue = '';
        itemval.argumentsCode = '';
      });
    }
    this.$set(item, 'paramTypeList', resetList);
    this.dragNodeData = {};
  }

  // 保存时，整理决策编辑器的参数，主要是日期要拼接
  handleSaveConcatParams(val) {
    if (val && val.children.length > 0) {
      val.children.forEach((item) => {
        if (item && item.children.length > 0) {
          this.handleSaveConcatParams(item);
        }
        const condition = item.condition;
        if (condition['fieldTypeEvent'] === 4) {
          const parameterInit = function(val) {
            const parameter: Array<any> = [];
            val.forEach((item2: any) => {
              if (item2.fieldTypeEvent == 4) {
                parameter.push(
                  item2.argumentsCode +
                    '(' +
                    parameterInit(item2.paramTypeList).join(',') +
                    ')'
                );
              } else if (item2.format === 0) {
                parameter.push(item2.argumentsCode);
              } else if (item2.argumentsCode) {
                parameter.push('"${' + item2.argumentsCode + '}"');
              } else {
                parameter.push(`"${item2.paramValue}"`);
              }
            });
            return parameter;
          };
          const newarr = parameterInit(condition.paramTypeList);
          condition['right'] = `${condition.leftEvent}(${newarr.join(',')})`;
          return;
        } else if (condition['fieldTypeEvent'] === 'notFun') {
          condition['right'] = condition.leftEvent;
        }
      });
    } else {
      if (!this.editorRuleTree[0].script) {
        this.editorRuleTree = [];
      }
    }
    return cloneDeep(this.editorRuleTree);
  }
  // 解析日期表达式
  splitParams(str1) {
    // const str1 = "addDateForDay(addDateForMonth(getDate(), -5), 10)";
    // const str = "addDateForDay(addDateForMonth(2019-09-08,-5),10)";
    const str = str1.replace(/\s*/g, '');
    if (str.indexOf(',') === -1) {
      if (str === 'getDate()') {
        this.formatDate.dateSel = 'systemNow';
        this.formatDate.baseDate = 'getDate()';
        this.formatDate.showDatePicker = false;
      } else {
        this.formatDate.dateSel = 'selDate';
        this.formatDate.baseDate = str;
        this.formatDate.showDatePicker = true;
      }
    } else {
      const arr1 = str.replace(/\)|\(/g, ',').split(',');
      const arr2 = arr1.filter((item) => item !== '');
      this.formatDate.offsetList = [];
      // type: 1-年， 2-月，3-日
      const baseTotal = arr2.length - 1;
      // mmmmmm
      if (
        arr2[0] == 'getCurrentQuarterTime' ||
        arr2[0] == 'getCurrentMoonTime'
      ) {
        // arr2 = ['getCurrentQuarterTime', '-2', 1]
        const obj: never | object = {};
        if (arr2[0] == 'getCurrentQuarterTime') {
          obj['type'] = arr2[2] == 1 ? 4 : arr2[2] == 2 ? 5 : 0;
          this.formatDate.dateSel = 'currentQuarter';
        } else if (arr2[0] == 'getCurrentMoonTime') {
          obj['type'] = arr2[2] == 1 ? 6 : arr2[2] == 2 ? 7 : 0;
          this.formatDate.dateSel = 'currentMonth';
        }

        const sign = arr2[1].substr(0, 1);
        if (sign === '-') {
          obj['sign'] = '-';
          obj['offset'] = parseInt(arr2[1].substr(1));
        } else {
          obj['sign'] = '+';
          obj['offset'] = parseInt(arr2[1]);
        }

        this.formatDate.showDatePicker = false;
        this.formatDate.offsetList.unshift(obj);
      } else {
        arr2.forEach((item, index) => {
          const obj: never | object = {};
          if (index < baseTotal / 2) {
            const baseCount = arr2[baseTotal - index];
            const sign = baseCount.substr(0, 1);
            obj['type'] =
              item === 'addDateForYear'
                ? 1
                : item === 'addDateForMonth'
                ? 2
                : item === 'addDateForDay'
                ? 3
                : 0;
            if (sign === '-') {
              obj['sign'] = '-';
              obj['offset'] = parseInt(baseCount.substr(1));
            } else {
              obj['sign'] = '+';
              obj['offset'] = parseInt(baseCount);
            }
            this.formatDate.offsetList.unshift(obj);
          } else if (index === baseTotal / 2) {
            if (item === 'getDate') {
              this.formatDate.dateSel = 'systemNow';
              this.formatDate.baseDate = 'getDate()';
              this.formatDate.showDatePicker = false;
            }
          } else {
            return;
          }
        });
      }
    }
    return this.formatDate;
  }
}
